<template><section><html><head></head><body><h1>FrustumGeometry, FrustumOutlineGeometry</h1>
<ul>
<li>The <code v-pre="">vc-geometry-frustum</code> component is used to load a geometry that describes a frustum at the given the origin and orientation.</li>
<li>The <code v-pre="">vc-geometry-outline-frustum</code> component is used to load a geometry that describes the outline of a frustum with the given the origin and orientation.</li>
<li>It needs to be used as a sub-component of <code v-pre="">vc-instance-geometry</code>, can be mounted on <code v-pre="">vc-primitive</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load FrustumGeometry</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @left_click="LEFT_CLICK">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-frustum ref="frustum" :frustum="frustum" :origin="origin" :orientation="orientation" :vertex-format="vertexFormat"></vc-geometry-frustum>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-frustum ref="frustumOutline" :frustum="frustum" :origin="originOutline" :orientation="orientation"></vc-geometry-outline-frustum>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
